      <!-- Ï£ºÎ¨∏ ÌíàÎ™© Ï†ïÎ≥¥ -->
      <div class="order-detail-section">
        <h4>[Ï£ºÎ¨∏ ÌíàÎ™© Ï†ïÎ≥¥]</h4>
        <div class="item-table-wrapper">
