</template>

<script setup lang="ts">
// üîπ Vue 3 Composition API Ìï®ÏàòÎì§ÏùÑ Í∞ÄÏ†∏ÏòµÎãàÎã§
import { ref, computed, onMounted, watch } from 'vue'
// üîπ HTTP ÏöîÏ≤≠ÏùÑ ÏúÑÌïú axios ÎùºÏù¥Î∏åÎü¨Î¶¨
import axios from 'axios'

// üîπ TypeScript Ïù∏ÌÑ∞ÌéòÏù¥Ïä§: Îç∞Ïù¥ÌÑ∞Ïùò Íµ¨Ï°∞Î•º Ï†ïÏùòÌï©ÎãàÎã§
interface Order {
  order_id: string          // Ï£ºÎ¨∏Î≤àÌò∏
  customer_name: string     // Í±∞ÎûòÏ≤òÎ™Ö
  customer_id: string       // Í±∞ÎûòÏ≤ò ID
  order_date: string        // Ï£ºÎ¨∏Ïùº
  delivery_date: string     // ÎÇ©Í∏∞Ïùº
  status: string           // ÏÉÅÌÉú
  phone: string            // Ïó∞ÎùΩÏ≤ò
  address: string          // Ï£ºÏÜå
  manager_name: string     // Îã¥ÎãπÏûêÎ™Ö
  created_by: string       // ÏûëÏÑ±Ïûê
}

interface OrderItem {
  product_id: string       // Ï†úÌíà ID
  product_name: string     // Ï†úÌíàÎ™Ö
  product_code: string     // Ï†úÌíàÏΩîÎìú
  spec: string            // Í∑úÍ≤©
  order_qty: number       // Ï£ºÎ¨∏ÏàòÎüâ
  stock_qty: number       // Ïû¨Í≥†ÏàòÎüâ
  note: string            // ÎπÑÍ≥†
}

// üîπ ref(): VueÏùò Î∞òÏùëÌòï Îç∞Ïù¥ÌÑ∞Î•º ÎßåÎì≠ÎãàÎã§
// Í∞íÏù¥ Î≥ÄÍ≤ΩÎêòÎ©¥ ÌôîÎ©¥Ïù¥ ÏûêÎèôÏúºÎ°ú ÏóÖÎç∞Ïù¥Ìä∏Îê©ÎãàÎã§
const searchText = ref('')                    // Í≤ÄÏÉâÏñ¥
const selectedIds = ref<string[]>([])         // ÏÑ†ÌÉùÎêú Ï£ºÎ¨∏ IDÎì§
const selectAll = ref(false)                  // Ï†ÑÏ≤¥ ÏÑ†ÌÉù Ïó¨Î∂Ä
const currentPage = ref(1)                    // ÌòÑÏû¨ ÌéòÏù¥ÏßÄ
const itemsPerPage = ref(10)                  // ÌéòÏù¥ÏßÄÎãπ Ìï≠Î™© Ïàò
const orders = ref<Order[]>([])               // Ï†ÑÏ≤¥ Ï£ºÎ¨∏ Î™©Î°ù
const selectedOrder = ref<Order | null>(null) // ÏÑ†ÌÉùÎêú Ï£ºÎ¨∏
const orderItems = ref<OrderItem[]>([])       // ÏÑ†ÌÉùÎêú Ï£ºÎ¨∏Ïùò ÌíàÎ™©Îì§
const loading = ref(false)                    // Î°úÎî© ÏÉÅÌÉú

// üîπ ÌïÑÌÑ∞ ÏÉÅÌÉú
const filters = ref({
  customer: '',      // Í±∞ÎûòÏ≤òÎ™Ö ÌïÑÌÑ∞
  businessNo: '',    // ÏÇ¨ÏóÖÏûêÎ≤àÌò∏ ÌïÑÌÑ∞
  manager: '',       // Îã¥ÎãπÏûê ÌïÑÌÑ∞
  status: ''        // ÏÉÅÌÉú ÌïÑÌÑ∞
})

// üîπ ÌïÑÌÑ∞ ÏòµÏÖòÎì§ (DBÏóêÏÑú Í∞ÄÏ†∏Ïò® Í≥†Ïú†Í∞íÎì§)
const customerOptions = ref<string[]>([])
const businessNoOptions = ref<string[]>([])
const managerOptions = ref<string[]>([])
const statusOptions = ref(['ÏßÑÌñâÏ§ë', 'ÏôÑÎ£å', 'ÏßÄÏó∞', 'ÎåÄÍ∏∞', 'Ï∑®ÏÜå'])

// üîπ computed: Îã§Î•∏ Îç∞Ïù¥ÌÑ∞Î•º Í∏∞Î∞òÏúºÎ°ú Í≥ÑÏÇ∞ÎêòÎäî ÏÜçÏÑ±
// ÏùòÏ°¥ÌïòÎäî Îç∞Ïù¥ÌÑ∞Í∞Ä Î≥ÄÍ≤ΩÎêòÎ©¥ ÏûêÎèôÏúºÎ°ú Ïû¨Í≥ÑÏÇ∞Îê©ÎãàÎã§
const filteredOrders = computed(() => {
  return orders.value.filter(order => {
    // Í≤ÄÏÉâÏñ¥ ÌïÑÌÑ∞
    const matchesSearch = !searchText.value || 
      order.order_id.includes(searchText.value) ||
      order.customer_name.toLowerCase().includes(searchText.value.toLowerCase())
    
    // ÎìúÎ°≠Îã§Ïö¥ ÌïÑÌÑ∞
    const matchesFilters = 
      (!filters.value.customer || order.customer_name === filters.value.customer) &&
      (!filters.value.status || order.status === filters.value.status) &&
      (!filters.value.manager || order.manager_name === filters.value.manager)
    
    return matchesSearch && matchesFilters
  })
})

// üîπ Ï¥ù ÌéòÏù¥ÏßÄ Ïàò Í≥ÑÏÇ∞
const totalPages = computed(() => 
  Math.ceil(filteredOrders.value.length / itemsPerPage.value)
)

// üîπ ÌòÑÏû¨ ÌéòÏù¥ÏßÄÏùò ÏãúÏûë Ïù∏Îç±Ïä§
const startIndex = computed(() => 
  (currentPage.value - 1) * itemsPerPage.value
)

// üîπ ÌòÑÏû¨ ÌéòÏù¥ÏßÄÏóê ÌëúÏãúÌï† Ï£ºÎ¨∏Îì§
const paginatedOrders = computed(() => {
  const start = startIndex.value
  const end = start + itemsPerPage.value
  return filteredOrders.value.slice(start, end)
})

// üîπ onMounted: Ïª¥Ìè¨ÎÑåÌä∏Í∞Ä ÌôîÎ©¥Ïóê ÌëúÏãúÎê† Îïå Ïã§ÌñâÎê©ÎãàÎã§
onMounted(async () => {
  await fetchOrders()      // Ï£ºÎ¨∏ Î™©Î°ù Í∞ÄÏ†∏Ïò§Í∏∞
  await fetchFilterOptions() // ÌïÑÌÑ∞ ÏòµÏÖò Í∞ÄÏ†∏Ïò§Í∏∞
})

// üî∏ Mock Îç∞Ïù¥ÌÑ∞ Ìï®Ïàò (Í∞úÎ∞úÏö©)
function useMockData() {
  // Ï£ºÎ¨∏ Î™©Î°ù ÏÑ§Ï†ï
  orders.value = [
    {
      order_id: 'ORD001',
      customer_name: 'ÏÖÄÌä∏Î¶¨Ïò®',
      customer_id: 'CUS001',
      order_date: '2024-01-15',
      delivery_date: '2024-01-20',
      status: 'ÏßÑÌñâÏ§ë',
      phone: '02-1234-5678',
      address: 'Ïù∏Ï≤úÍ¥ëÏó≠Ïãú Ïó∞ÏàòÍµ¨ ÏïÑÏπ¥Îç∞ÎØ∏Î°ú 23',
      manager_name: 'ÌôçÍ∏∏Îèô',
      created_by: 'ÍπÄÏ≤†Ïàò'
    },
    {
      order_id: 'ORD002',
      customer_name: 'ÌïúÎØ∏ÏïΩÌíà',
      customer_id: 'CUS002',
      order_date: '2024-01-16',
      delivery_date: '2024-01-22',
      status: 'ÏôÑÎ£å',
      phone: '02-9876-5432',
      address: 'ÏÑúÏö∏ÌäπÎ≥ÑÏãú ÏÜ°ÌååÍµ¨ ÏúÑÎ°ÄÏÑ±ÎåÄÎ°ú 14',
      manager_name: 'Ïù¥ÏòÅÌù¨',
      created_by: 'Î∞ïÏßÄÎØº'
    },
    {
      order_id: 'ORD003',
      customer_name: 'Ï¢ÖÍ∑ºÎãπ',
      customer_id: 'CUS003',
      order_date: '2024-01-17',
      delivery_date: '2024-01-25',
      status: 'ÏßÄÏó∞',
      phone: '02-5555-1234',
      address: 'ÏÑúÏö∏ÌäπÎ≥ÑÏãú Ïö©ÏÇ∞Íµ¨ Ï≤≠ÌååÎ°ú 383',
      manager_name: 'ÌôçÍ∏∏Îèô',
      created_by: 'ÍπÄÏ≤†Ïàò'
    },
    {
      order_id: 'ORD004',
      customer_name: 'ÎåÄÏõÖÏ†úÏïΩ',
      customer_id: 'CUS004',
      order_date: '2024-01-18',
      delivery_date: '2024-01-28',
      status: 'ÎåÄÍ∏∞',
      phone: '02-3333-4444',
      address: 'ÏÑúÏö∏Ïãú Í∞ïÎÇ®Íµ¨ Î¥âÏùÄÏÇ¨Î°ú 114Í∏∏ 12',
      manager_name: 'Î∞ïÏßÄÎØº',
      created_by: 'Ïù¥ÏòÅÌù¨'
    }
  ]
  
  // ÌïÑÌÑ∞ ÏòµÏÖò ÏÑ§Ï†ï
  customerOptions.value = ['ÏÖÄÌä∏Î¶¨Ïò®', 'ÌïúÎØ∏ÏïΩÌíà', 'Ï¢ÖÍ∑ºÎãπ', 'ÎåÄÏõÖÏ†úÏïΩ', 'Ïú†ÌïúÏñëÌñâ']
  managerOptions.value = ['ÌôçÍ∏∏Îèô', 'ÍπÄÏ≤†Ïàò', 'Ïù¥ÏòÅÌù¨', 'Î∞ïÏßÄÎØº']
  businessNoOptions.value = ['111-11-11111', '222-22-22222', '333-33-33333']
  
  // Ï≤´ Î≤àÏß∏ Ï£ºÎ¨∏ ÏûêÎèô ÏÑ†ÌÉù
  if (orders.value.length > 0) {
    selectOrder(orders.value[0])
  }
}

// üîπ API Ìï®ÏàòÎì§
// async/await: ÎπÑÎèôÍ∏∞ ÏûëÏóÖÏùÑ ÎèôÍ∏∞Ï†ÅÏúºÎ°ú Ï≤òÎ¶¨Ìï©ÎãàÎã§
async function fetchOrders() {
  try {
    loading.value = true
    // GET ÏöîÏ≤≠: ÏÑúÎ≤ÑÏóêÏÑú Ï£ºÎ¨∏ Î™©Î°ùÏùÑ Í∞ÄÏ†∏ÏòµÎãàÎã§
    const response = await axios.get('/api/orders')
    
    // üî∏ ÎîîÎ≤ÑÍπÖ: Î∞õÏùÄ Îç∞Ïù¥ÌÑ∞ ÌôïÏù∏
    console.log('API ÏùëÎãµ Îç∞Ïù¥ÌÑ∞:', response.data)
    
    // üî∏ Îç∞Ïù¥ÌÑ∞Í∞Ä Î∞∞Ïó¥Ïù∏ÏßÄ ÌôïÏù∏ÌïòÍ≥† Ï≤òÎ¶¨
    if (Array.isArray(response.data)) {
      orders.value = response.data
    } else if (response.data && Array.isArray(response.data.data)) {
      // ÎßåÏïΩ { data: [...] } ÌòïÌÉúÎ°ú ÏôîÎã§Î©¥
      orders.value = response.data.data
    } else if (response.data && Array.isArray(response.data.orders)) {
      // ÎßåÏïΩ { orders: [...] } ÌòïÌÉúÎ°ú ÏôîÎã§Î©¥
      orders.value = response.data.orders
    } else {
      console.error('ÏòàÏÉÅÏπò Î™ªÌïú Îç∞Ïù¥ÌÑ∞ ÌòïÏãù:', response.data)
      orders.value = []
    }
    
    // Ï≤´ Ï£ºÎ¨∏ ÏûêÎèô ÏÑ†ÌÉù
    if (orders.value.length > 0) {
      await selectOrder(orders.value[0])
    }
  } catch (error) {
    console.error('Ï£ºÎ¨∏ Î™©Î°ù Î°úÎìú Ïã§Ìå®:', error)
    // APIÍ∞Ä ÏïÑÏßÅ ÏóÜÏúºÎ©¥ ÏûÑÏãú Îç∞Ïù¥ÌÑ∞ ÏÇ¨Ïö©
    orders.value = [
      {
        order_id: 'ORD001',
        customer_name: 'ÏÖÄÌä∏Î¶¨Ïò®',
        customer_id: 'CUS001',
        order_date: '2024-01-15',
        delivery_date: '2024-01-20',
        status: 'ÏßÑÌñâÏ§ë',
        phone: '02-1234-5678',
        address: 'Ïù∏Ï≤úÍ¥ëÏó≠Ïãú Ïó∞ÏàòÍµ¨ ÏïÑÏπ¥Îç∞ÎØ∏Î°ú 23',
        manager_name: 'ÌôçÍ∏∏Îèô',
        created_by: 'ÍπÄÏ≤†Ïàò'
      },
      {
        order_id: 'ORD002',
        customer_name: 'ÌïúÎØ∏ÏïΩÌíà',
        customer_id: 'CUS002',
        order_date: '2024-01-16',
        delivery_date: '2024-01-22',
        status: 'ÏôÑÎ£å',
        phone: '02-9876-5432',
        address: 'ÏÑúÏö∏ÌäπÎ≥ÑÏãú ÏÜ°ÌååÍµ¨ ÏúÑÎ°ÄÏÑ±ÎåÄÎ°ú 14',
        manager_name: 'Ïù¥ÏòÅÌù¨',
        created_by: 'Î∞ïÏßÄÎØº'
      }
    ]
  } finally {
    loading.value = false
  }
}

// üîπ ÌïÑÌÑ∞ ÏòµÏÖò Í∞ÄÏ†∏Ïò§Í∏∞
async function fetchFilterOptions() {
  try {
    // Í±∞ÎûòÏ≤òÎ™Ö Î™©Î°ù Í∞ÄÏ†∏Ïò§Í∏∞
    const customersRes = await axios.get('/api/customers/names')
    customerOptions.value = customersRes.data
    
    // Îã¥ÎãπÏûê Î™©Î°ù Í∞ÄÏ†∏Ïò§Í∏∞
