// üîπ Ï£ºÎ¨∏ ÏÑ†ÌÉù Ïãú ÏÉÅÏÑ∏ Ï†ïÎ≥¥ Í∞ÄÏ†∏Ïò§Í∏∞
async function selectOrder(order: Order) {
  try {
    selectedOrder.value = order
    
    // üî∏ Î∞±ÏóîÎìú API Í≤ΩÎ°úÏóê ÎßûÍ≤å ÏàòÏ†ï (/details)
    const response = await axios.get(`/api/orders/${order.order_id}/details`)
    console.log('Ï£ºÎ¨∏ ÏÉÅÏÑ∏ ÏùëÎãµ:', response.data)
    
    // üî∏ Î∞±ÏóîÎìú ÏùëÎãµ Íµ¨Ï°∞Ïóê ÎßûÍ≤å Îç∞Ïù¥ÌÑ∞ Ï≤òÎ¶¨
    if (response.data) {
      // orderÏôÄ itemsÍ∞Ä Î∂ÑÎ¶¨ÎêòÏñ¥ ÏûàÏùÑ Í≤ΩÏö∞
      if (response.data.items) {
        orderItems.value = response.data.items
      } 
      // ÎòêÎäî Îã§Î•∏ Íµ¨Ï°∞Ïùº Í≤ΩÏö∞ (Î∞±ÏóîÎìú ÏùëÎãµ ÌôïÏù∏ ÌïÑÏöî)
      else if (Array.isArray(response.data)) {
        orderItems.value = response.data
      } else {
        console.log('ÏòàÏÉÅÏπò Î™ªÌïú ÏÉÅÏÑ∏ Îç∞Ïù¥ÌÑ∞ Íµ¨Ï°∞:', response.data)
        orderItems.value = []
      }
    }
  } catch (error) {
    console.error('Ï£ºÎ¨∏ ÏÉÅÏÑ∏ Ï†ïÎ≥¥ Î°úÎìú Ïã§Ìå®:', error)
    
    // üî∏ ÏûÑÏãú Îç∞Ïù¥ÌÑ∞ ÏÇ¨Ïö©
    orderItems.value = [
      {
        product_id: 'PRD001',
        product_name: 'ÌÉÄÏù¥Î†àÎÜÄ 500mg',
        product_code: 'TYL-500',
        spec: '500mg x 10Ï†ï',
        order_qty: 100,
        stock_qty: 50,
        note: 'Í∏¥Í∏â Î∞∞ÏÜ° ÏöîÏ≤≠'
      },
      {
        product_id: 'PRD002',
        product_name: 'ÏïÑÏä§ÌîºÎ¶∞ 100mg',
        product_code: 'ASP-100',
        spec: '100mg x 30Ï†ï',
