}

// üîπ Í≤ÄÏÉâ Ïã§Ìñâ
function handleSearch() {
  currentPage.value = 1  // Í≤ÄÏÉâ Ïãú Ï≤´ ÌéòÏù¥ÏßÄÎ°ú Ïù¥Îèô
}

// üîπ Ï†ÑÏ≤¥ ÏÑ†ÌÉù/Ìï¥Ï†ú
function handleSelectAll(value: boolean) {
  if (value) {
    // ÌòÑÏû¨ ÌéòÏù¥ÏßÄÏùò Î™®Îì† Ï£ºÎ¨∏ ÏÑ†ÌÉù
    selectedIds.value = paginatedOrders.value.map(order => order.order_id)
  } else {
    // Î™®Îëê ÏÑ†ÌÉù Ìï¥Ï†ú
    selectedIds.value = []
  }
}

// üîπ ÎÇ†Ïßú Ìè¨Îß∑ÌåÖ Ìï®Ïàò
function formatDate(dateString: string) {
  if (!dateString) return '-'
  // YYYY-MM-DD ÌòïÏãùÏúºÎ°ú Î≥ÄÌôò
  const date = new Date(dateString)
  return date.toLocaleDateString('ko-KR').replace(/\. /g, '-').replace(/\./g, '')
}

// üîπ ÏÉÅÌÉúÎ≥Ñ ÏÉâÏÉÅ Î∞òÌôò
function getStatusColor(status: string) {
  const colorMap: Record<string, string> = {
    'ÏßÑÌñâÏ§ë': 'info',
    'ÏôÑÎ£å': 'success',
    'ÏßÄÏó∞': 'danger',
    'ÎåÄÍ∏∞': 'warning',
    'Ï∑®ÏÜå': 'secondary'
  }
  return colorMap[status] || 'secondary'
}

// üîπ watch: ÌäπÏ†ï Îç∞Ïù¥ÌÑ∞Ïùò Î≥ÄÍ≤ΩÏùÑ Í∞êÏßÄÌï©ÎãàÎã§
watch(filters, () => {
  // ÌïÑÌÑ∞Í∞Ä Î≥ÄÍ≤ΩÎêòÎ©¥ Ï≤´ ÌéòÏù¥ÏßÄÎ°ú Ïù¥Îèô
  currentPage.value = 1
}, { deep: true })  // deep: Í∞ùÏ≤¥ ÎÇ¥Î∂ÄÏùò Î≥ÄÍ≤ΩÎèÑ Í∞êÏßÄ

// üîπ ÌéòÏù¥ÏßÄÎãπ Ìï≠Î™© Ïàò Î≥ÄÍ≤Ω Ïãú
watch(itemsPerPage, () => {
  currentPage.value = 1
})
